.body {
    width: 100%;
    padding: 40px;
    box-sizing: border-box;
    background-color: #FFF;
    border-radius: 5px;
}

.table {
    border: 1px solid #000;
    width: 70%;
    display: grid;
    grid-template-columns: 3fr 9fr;
}

.tableItem {
    border: 1px solid #000;
    padding: 5px;
}
