.list {
    background-color: #FFF;
    border: none;
    border-radius: 5px;
    height: 30px;
    max-width: 100%;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    display: flex;
    align-items: center;
    text-wrap: wrap;
    color: #1E1E1F;

    padding: 4px;
}